    padding: 0;
    margin: 0;
}

body {
    font-family: 'poppins', sans-serif;
    background-color: white;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}

/* style skip to konten */
.skip-link{
  position: absolute;
  top: -40px;
  left: 0;
  padding: 8px;
  z-index: 100;
  background-color: brown;
  color: white;
}

.skip-link:focus {
  top: 0;
}

/* app bar */

.app-bar {
    padding: 8px 16px;
    background-color: #007F73;
    display: grid;
    grid-template-columns: auto 1fr auto;
    gap: 10px;
    position: sticky;
    top: 0;
    z-index: 99;
    box-shadow: 0 2px 4px rgba(0, 0, 0.2,);
}

.app-bar .app-bar__menu {
    display: flex;
    align-items: center;
}

.app-bar .app-bar__menu button {
    background-color: transparent;
    border: none;
    font-size: 18px;
    padding: 0.8em;
    cursor: pointer;
}

.app-bar .app-bar__title {
    display: flex;
    align-items: center;
  }
   
  .app-bar .app-bar__title h1 a {
    color: yellow;
    text-transform: uppercase;
    text-decoration: none;
    font-size: 22px;
    padding: 0.8em;
   
  }
   
  .app-bar .app-bar__navigation {
    position: absolute;
    top: 50px;
    left: -180px;
    width: 150px;
    transition: all 0.3s;
    padding: 8px;
    background-color: #007F73;
    overflow: hidden;
  }
   
  .app-bar .app-bar__navigation.open {
    left: 0;
  }
   
  .app-bar .app-bar__navigation ul li a {
    display: inline-block;
    text-decoration: none;
    color: yellow;
    padding: 1em;
    margin-bottom: 5px;
    width: 100%;
  }

  /* Untuk Jumbroton */

  .jumbroton{
    width: 100%;
    display: flex;
    align-items: center;
    text-align: center;
    height: 400px;
    background-image:linear-gradient(rgba(0,0,5,0.5), rgba(0,0,5,0.5)),url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-position: center;
    background-color: aqua;
  }
  
  .jumbroton-inner {
    margin: 0 auto;
    max-width: 800px;
  }
  
  .jumbroton-titel {
    color: yellow;
    font-weight: 500px;
    font-size: 36px;
    text-align: center;
  }
  
  .jumbroton-description{
    color: yellow;
    margin-top: 16px;
    font-size: 18px;
    font-weight: 300px;
  }

  /* Main Content */
main {
  width: 100%;
  margin: 0 auto;
}

.content {
  padding: 25px;
  text-align: left;
  overflow: auto;
}

.latest {
  width: 100%;
  margin: 20px auto;
  text-align: center;
}

.latesthead {
  padding-top: 40px;
  text-align: center;
  color: black;
}

.contentlist {
  margin: 10px 0;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  width: 100%;
  border-radius: 5px;
  overflow: hidden;
  background-color: #ffffff;
}

.contentlist a{
  text-decoration: none;
  color: #393646;
}

.image {
  width: 100%;
  max-height: 250px;
}

.restaurant_info {
  padding: 18px 32px 32px 32px;
  color: black;
}

.info_title{
  text-align: center;
  padding: 0.8em;
}

.info_title:hover {
  opacity: 0.7;
}

.info_city {
  text-align: center;
  padding: 0.8em;
}

.info_desc {
  margin-top: 5px;
  text-align: justify;
}

.info_rates {
  color: yellow;
  text-align: center;
  margin-top: 5px;
  padding: 0.8em;
  font-style: italic;
  border-radius: 5px 5px 5px;
  background-color: #007F73;
}


  /* halaman detail */
.contentdetail {
  margin: 0 auto;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  gap: 18px 16px;
}

.contentdetail .restaurant_info_detail{
  margin: 0 auto;
  width: 100%;
  max-width: 800px;
  display: grid;
  grid-template-columns: 1fr;
  gap: 18px 16px;
}

.contentdetail .detail_title{
  margin-top: 50px;
  text-align: center;
  
}

.contentdetail .image_detail {
  width: 80%;
  padding: 10px;
  margin: 0 auto;
}

.contentdetail .detail_list{
  margin-bottom: 50px;
  padding: 1em;
}

.contentdetail .detail_list h4 {
  margin: 8px 0;
}

/* untuk makanan dan minuman */
.menuContainer {
  display: grid;
  grid-template-columns: repeat(2, 1fr); /* Menggunakan fungsi repeat() untuk lebih mudah mengatur jumlah kolom */
  gap: 20px; /* Menggunakan gap sebagai pengganti grid-column-gap */
  margin: 30px;
  margin-left: 30px; /* Anda bisa menggabungkan margin menjadi margin: 30px 30px 30px 0; untuk lebih singkat */
}

.foodMenu,
.drinkMenu {
  margin-bottom: 20px;
  padding: 20px;
  display: flex;
  flex-direction: column; 
  align-items: center;
  border-radius: 8px;
  box-shadow: 0 4px 5px rgba(0, 0, 0, 0.1); 
  background-color: #007F73;
  color: yellow;
}

.foodMenu ul,
.drinkMenu ul {
  list-style-type: decimal;
  padding-left: 20px; 
}

.foodMenu h3,
.drinkMenu h3 {
  margin-bottom: 10px;
  color: white; 
}

.foodMenu li,
.drinkMenu li {
  margin-bottom: 5px;
  font-size: 16px; 
  line-height: 1.4; 
}


/* untuk ulasan */
.reviewCust {
  padding: 10px;
}

.headerInformation {
  font-size: 1.5em;
  margin-bottom: 10px;
  text-align: center;
}

.customerReviews {
  display: flex;
  flex-wrap: wrap;
  background-color: #007F73;
  color: yellow;
}

.customerReview {
  flex: 1 1 300px; 
  margin: 5px;
  padding: 10px;
  border: 2px solid yellow;
}


/* fitur like */
.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #007F73;

  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;

  font-size: 18px;
  color: yellow;
  cursor: pointer;
}

/* untuk reviewer */
.title-review {
  font-size: 24px;
  margin-bottom: 20px;
  color: #333;
  text-align: center;
}

form {
  margin-bottom: 40px;
  margin: 10px;
  border: 1px solid #ccc;
  padding: 20px;
  border-radius: 5px;
  background-color: #007F73;
}

label {
  display: block;
  margin-bottom: 5px;
  color: yellow;
}

input[type="text"],
textarea {
  width: 80%;
  padding: 1em;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

#title-button {
  background-color: yellow;
  color: black;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  text-align: center;
  margin: 0 auto;
  padding: 1.2em;
}

button:hover {
  background-color: #45a049;
}

#customerReviews {
  border-top: 1px solid #ccc;
  padding-top: 20px;
}

.review {
  margin-bottom: 10px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.review strong {
  font-weight: bold;
}

.review span {
  font-style: italic;
  color: #666;
}


/* ini adalah style footer */
footer{
  width: 100%;
  text-align: center;
  font-size: 1rem;
  margin-bottom: 0;
  color: yellow;
  background-color: #007F73;
}
footer p{
  padding: 1em;
}
